.stocks {
  &__items {
    margin: 30px 0 0;
    @include screen(laptop) {
      margin: 50px 0 30px;
    }
  }
  &__item {
    padding: 20px 0;
    border-top: 1px solid #DAD8E1;
    display: flex;
    flex-direction: column;
    gap: 25px;
    &:last-child {
      border-bottom: 1px solid #DAD8E1;
    }
    @include screen(laptop) {
      flex-direction: row;
      gap: 72px;
      padding: 60px 40px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    &-reversed {
      .stocks__content {
        @include screen(laptop) {
          order: -1;
        }
      }
    }
  }
  &__heading {
    display: flex;
    align-items: center;
    z-index: 2;
    position: relative;
    h3 {
      font-family: $gotham;
      font-weight: 500;
      font-size: 48px;
      line-height: 57px;
      margin-right: 17px;
      @include screen(laptop) {
        margin-right: 27px;
        font-size: 90px;
        line-height: 108px;
      }
    }
    span {
      font-family: $gotham;
      font-weight: 500;
      margin-bottom: 8px;
      font-size: 18px;
      line-height: 21px;
      @include screen(laptop) {
        font-size: 28px;
        line-height: 33px;
      }
    }
  }
  &__image {
    padding: 24px 31px 30px;
    width: 100%;
    min-height: 215px;
    background-color: rgba(#D3E8E6, 0.5);
    position: relative;
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    @include screen(laptop) {
      min-height: 283px;
      padding: 29px 55px;
    }
    p {
      font-size: 12px;
      line-height: 16px;
      max-width: 189px;
      z-index: 2;
      position: relative;
      @include screen(laptop) {
        font-size: 16px;
        max-width: 313px;
        line-height: 22px;
      }
    }
    time {
      margin-top: 20px;
      display: inline-block;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      z-index: 2;
      position: relative;
      padding: 7px 11px;
      border: 1px solid #817F87;
      border-radius: 12px;
      @include screen(laptop) {
        margin-top: 30px;
        padding: 7px 15px;
        font-size: 16px;
        line-height: 22px;
      }
    }
  }
  &__illustration {
    position: absolute;
    top: 20px;
    bottom: 0;
    display: flex;
    max-width: 60%;
    align-items: flex-end;
    right: 0;
    img {
      object-fit: contain;
      max-height: 215px;
      @include screen(laptop) {
        max-width: 403px;
      }
    }
  }
  &__bg {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 100%;
    img {
      height: 100%;
    }
  }
  &__subcontent {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 17px;
    @include screen(laptop) {
      padding: 0 17px 30px;
    }
  }
  &__title {
    font-family: $gotham;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    @include screen(laptop) {
      font-size: 28px;
      line-height: 36px;
    }
  }
  &__time {
    margin-top: 15px;
    display: flex;
    align-items: center;
    gap: 5px;
    span {
      color: #545459;
      font-size: 14px;
      line-height: 19xp;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 24px;
      }
    }
  }
  &__link {
    padding: 17px 26px;
    text-transform: uppercase;
    color: $white;
    display: inline-block;
    font-family: $gotham;
    background-color: $text;
    border-radius: 12px;
    font-weight: 500;
    margin-top: 25px;
    font-size: 11px;
    line-height: 10px;
    @include screen(laptop) {
      font-size: 14px;
      margin-top: 30px;
      padding: 18px 26px;
      line-height: 13px;
    }
  }
}