.filters {
  margin-top: 20px;
  @include screen(laptop) {
    margin-top: 40px;
  }
  &__title {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 20px;
    svg {
      transition: transform $transition-default;
    }
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
    h4 {
      font-size: 16px;
      font-family: $gotham;
      font-weight: 500;
      line-height: 20px;
    }
    svg {
      margin-right: 3px;
    }
    @include screen(laptop) {
      display: none;
    }
  }
  &__items {
    position: fixed;
    bottom: 0;
    left: 0;
    top: auto;
    transform: translateY(100%);
    max-height: calc(100dvh - 15px);
    z-index: 105;
    overflow-y: auto;
    background-color: $white;
    border-radius: 26px 26px 0 0;
    transition: transform $transition-default;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 85px;
    @include screen(laptop) {
      padding-bottom: 0;
    }
    h3 {
      padding: 30px 19px 15px;
    }
    @include screen(laptop) {
      position: relative;
      transform: translateY(0);
      max-height: 100%;
      align-items: center;
      overflow: inherit;
      gap: 24px;
      display: grid;
      grid-template-columns: repeat(5, minmax(0, 1fr));
    }
    &-search {
      @include screen(laptop) {
        grid-template-columns: repeat(6, minmax(0, 1fr));
      }
    }
    &.active {
      transform: translateY(0);
      z-index: 1050;
    }
    .filters__dropdown {
      display: flex;
      border-top: 1px solid #DAD8E1;
      flex-direction: column;
      margin: 0 19px;
      max-height: 100%;
      padding: 0 0 20px 0;
      width: calc(100% - 38px);
      transform: translateY(0);
      position: static;
      border-radius: 0;
      box-shadow: none;
      &.no-padding {
        padding-bottom: 0;
      }
      @include screen(laptop) {
        border-radius: 26px 26px 0 0;
        padding: 10px 0;
        overflow-y: auto;
        margin: 0;
        max-height: inherit;
        position: absolute;
        transform: translateY(0);
        z-index: 1030;
        bottom: auto;
        transition: transform $transition-default;
        background-color: $white;
        box-shadow: 0px 0px 20px 0px #38364129;
        width: 100%;
        border: 1px solid #DAD8E1;
        max-height: 300px;
        border-radius: 12px;
        top: 69px;
        display: none;
      }
      p.main {
        padding-top: 0;
        @include screen(laptop) {
          padding-top: 10px;
        }
      }
      p {
        &:last-child {
          border-bottom: none;
          padding-bottom: 0;
          @include screen(laptop) {
            padding-bottom: 10px;
          }
        }
      }
    }
    .filters__area {
      padding: 0;
      margin-top: 10px;
      @include screen(laptop) {
        padding: 15px 25px 10px;
      }
    }
    .filters__current {
      display: none;
      @include screen(laptop) {
        display: flex;
      }
    }
    .filters__labels {
      padding: 0;
      @include screen(laptop) {
        padding: 10px 20px;
      }
    }
    .filters__cost {
      max-width: 100%;
      @include screen(laptop) {
        max-width: 162px;
      }
    }
    .filters__value {
      width: 100%;
    }
    .filters__slider {
      width: calc(100% - 20px);
      margin: 0 auto;
      @include screen(laptop) {
        width: 100%;
      }
    }
    .filters__title {
      padding-bottom: 20px;
      h4 {
        margin-bottom: 0;
      }
    }
  }
  &__item {
    flex: 1;
    @include screen(laptop) {
      position: relative;
    }
  }
  &__labels {
    padding: 10px 20px 5px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
  &__label {
    cursor: pointer;
    padding-left: 32px;
    width: 100%;
    position: relative;
    input {
      position: absolute;
      visibility: hidden;
      width: 0;
      height: 0;
      opacity: 0;
      &:checked + span {
        &:before {
          background-color: #545163;
        }
        &:after {
          opacity: 1;
        }
      }
    }
    span {
      position: relative;
      &:before {
        content: '';
        left: -32px;
        position: absolute;
        border: 1px solid #545163;
        border-radius: 6px;
        width: 20px;
        top: 2px;
        transition: background-color $transition-default;
        height: 20px;
      }
      &:after {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        left: -27px;
        top: -2px;
        opacity: 0;
        transition: opacity $transition-default;
      }
    }
  }
  &__current {
    border: 1px solid #DAD8E1;
    padding: 16.5px 25px;
    min-height: 57px;
    cursor: pointer;
    gap: 13px;
    border-radius: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
      position: absolute;
      visibility: hidden;
      width: 0;
      height: 0;
      opacity: 0;
      background-color: transparent;
    }
    svg {
      transition: transform $transition-default;
      min-width: 13px;
    }
    p {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden; 
      max-width: 170px;
      font-size: 14px;
      line-height: 20px;
      @include screen(laptop) {
        font-size: 16px;
      }
    }
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__bg {
    position: fixed;
    inset: 0;
    height: 100dvh;
    width: 100%;
    transition: visibility $transition-default, z-index $transition-default, opacity $transition-default;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    background-color: rgba($text, 0.2);
    @include screen(laptop) {
      display: none;
    }
    &.active {
      z-index: 1020;
      visibility: visible;
      opacity: 1;
    }
  }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 20px;
    top: 20px;
    @include screen(laptop) {
      display: none;
    }
  }
  &__dropdown {
    border-radius: 26px 26px 0 0;
    padding: 30px 19px;
    overflow-y: auto;
    max-height: calc(100dvh - 15px);
    position: fixed;
    transform: translateY(100%);
    z-index: 1030;
    bottom: 0;
    transition: transform $transition-default;
    background-color: $white;
    box-shadow: 0px 0px 20px 0px #38364129;
    width: 100%;
    @include screen(laptop) {
      border: 1px solid #DAD8E1;
      max-height: 300px;
      padding: 10px 0 15px;
      bottom: auto;
      position: absolute;
      border-radius: 12px;
      display: none;
      top: 69px;
      box-shadow: 0px 0px 20px 0px #36413D1F;
    }
    &.active {
      transform: translateY(0);
      @include screen(laptop) {
        display: flex;
      }
    }
    p {
      transition: background-color $transition-default, color $transition-default;
      padding: 16px 0;
      cursor: pointer;
      border-bottom: 1px solid #DAD8E1;
      font-size: 15px;
      line-height: 20px;
      position: relative;
      &:before {
        content: '';
        border-radius: 50%;
        position: absolute;
        right: 0;
        width: 20px;
        height: 20px;
        border: 1px solid #DAD8E1;
      }
      &.main {
        font-weight: 600;
      }
      &.active {
        &:before {
          border-color: #545163;
          border-width: 6px;
        }
        @include screen(laptop) {
          background-color: #F5F4F6;
        }
      }
      @include screen(laptop) {
        &:before {
          display: none;
        }
        border: none;
        padding: 10px 25px;
        &:hover {
          background-color: #F5F4F6;
        }
      }
    }
  }
  &__wrapper {
    h3 {
      margin-bottom: 10px;
      font-family: $gotham;
      font-size: 18px;
      font-weight: 500;
      line-height: 17px;
      @include screen(laptop) {
        display: none;
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 18px;
    margin-top: 15px;
    justify-content: space-between;
    flex-direction: row-reverse;
    @include screen(laptop) {
      padding-top: 25px;
      margin-top: 38px;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 0;
      border-top: 1px solid #DAD8E1;
    }
  }
  &__area {
    padding: 15px 25px 10px;
    .noUi-horizontal .noUi-handle {
      width: 20px;
      height: 20px;
      top: -9px;
      right: -9px;
    }
    .noUi-horizontal {
      box-shadow: none;
      height: 2px;
      background-color: #DAD8E1;
      border: none;
      border-radius: 0;
    }
    .noUi-connect {
      background-color: #887DA7;
    }
    .noUi-handle {
      border: 3px solid $white;
      border-radius: 7px;
      background-color: #887DA7;
      box-shadow: none;
    }
    .noUi-handle:before, .noUi-handle:after {
      display: none;
    }
  }
  &__costs {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }
  &__value {
    display: flex;
    gap: 15px;
    align-items: center;
    span {
      text-transform: lowercase;
    }
  }
  &__cost {
    max-width: 162px;
    width: 100%;
    background-color: #F2F3F5;
    padding: 10px 15px 9px;
    border-radius: 12px;
  }
  &__count {
    order: 2;
    width: 100%;
    font-size: 14px;
    margin-top: 20px;
    text-align: center;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 16px;
      width: auto;
      margin-top: 0;
      border-top: none;
      display: flex;
      align-items: center;
      order: inherit;
      padding-top: 0;
      line-height: 21px;
      min-height: 57px;
    }
  }
  &__tags {
    display: flex;
    width: 100%;
    gap: 15px;
    border-top: 1px solid #DAD8E1;
    overflow-x: auto;
    margin-top: 20px;
    @include screen(laptop) {
      flex-wrap: wrap;
      border-top: 0;
      padding-bottom: 0;
      margin-top: 0;
      padding-top: 0;
    }
    &.active {
      padding-top: 20px;
      margin-top: 20px;
      @include screen(laptop) {
        margin-top: 0;
      }
    }
  }
  &__remove {
    cursor: pointer;
  }
  &__tag {
    padding: 8px 16px;
    min-width: min-content;
    font-size: 14px;
    line-height: 20px;
    border-radius: 10px;
    display: flex;
    white-space: nowrap;
    background-color: #F2F3F5;
    gap: 10px;
    align-items: center;
    @include screen(laptop) {
      font-size: 17px;
      line-height: 23px;
      padding: 13px 16px;
    }
  }
  &__clear {
    padding: 8px 16px;
    order: 2;
    display: flex;
    font-size: 14px;
    line-height: 20px;
    white-space: nowrap;
    align-items: center;
    cursor: pointer;
    gap: 8px;
    @include screen(laptop) {
      padding: 13px 16px;
      font-size: 17px;
      line-height: 23px;
    }
  }
  &__trigger {
    background-color: $text;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 7px;
    border-radius: 12px;
    cursor: pointer;
    height: 42px;
    width: 47px;
    @include screen(laptop) {
      display: none;
    }
    span {
      display: none;
      font-size: 14px;
      color: #ACACB2;
      line-height: 20px;
    }
  }
  &__sorting {
    flex: 1;
    width: 100%;
    @include screen(laptop) {
      min-width: 250px;
      max-width: 250px;
    }
    .filters__current {
      background-color: #F2F3F5;
      border: none;
      padding: 11px 14px;
      min-height: 42px;
      @include screen(laptop) {
        min-height: 57px;
      }
    }
    .filters__dropdown {
      flex-direction: column;
      right: 0;
      @include screen(laptop) {
        min-width: 309px;
      }
    }
  }
  &__apply {
    padding: 15px 20px;
    background-color: $white;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: none;
    width: 100%;
    z-index: 1050;
    button {
      background-color: $text;
      text-transform: uppercase;
      color: $white;
      padding: 20px;
      font-size: 14px;
      font-weight: 500;
      line-height: 13px;
      font-family: $gotham;
      width: 100%;
      border-radius: 12px;
      text-align: center;
    }
  }
}