.search {
  &__wrapper  {
    padding-top: 30px;
    @include screen(laptop) {
      padding-top: 35px;
    }
  }
  &__results {
    display: flex;
    align-items: center;
    margin-top: 40px;
    padding-bottom: 12px;
    gap: 10px;
    overflow-x: auto;
    @include screen(laptop) {
      justify-content: center;
      padding-bottom: 0;
      overflow-x: inherit;
    }
  }
  &__result {
    display: flex;
    align-items: center;
    gap: 10px;
    border-radius: 14px;
    padding: 14px 24px;
    background-color: #F2F3F5;
    @include screen(laptop) {
      padding: 16.5px 39.5px;
      border-radius: 18px;
    
    }
    p {
      white-space: nowrap;
      font-size: 14px;
      line-height: 19px;
      text-align: center;
      @include screen(laptop) {
        font-size: 17px;
        line-height: 23px;
      }
    }
    span {
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      white-space: nowrap;
      color: #817F87;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
}